import React, { Component } from "react"
import add from "./add.module.scss"
import { addEvent } from "../actions/addEvent"
import moment from "moment"
import { connect } from "react-redux"
import uuid from "uuid"
import Cookies from "js-cookie"

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faPlusSquare } from "@fortawesome/free-solid-svg-icons"

class Add extends Component {
  state = {
    title: "",
    isError: false,
    note: "",
    noteOpen: false,
    positiveExp: 0,
    neutralExp: 1,
    negativeExp: 0,
  }
  handleInput = e => {
    this.setState({ [e.target.name]: e.target.value, isError: false })
  }

  submit = e => {
    e.preventDefault()
    if (this.state.input.length < 1 || this.state.note.length > 120) {
      this.setState({ error: true })
      return
    }
    const entryData = {
      title: this.props.category,
    }
    this.setState({
      title: "",
    })
    this.props.addEvent(entryData)
  }

  handleCheck = e => {
    if (e.target.name === "less") {
      this.setState({ less: !this.state.less, more: false })
    } else {
      this.setState({ less: false, more: !this.state.more })
    }
  }

  handleExp = e => {
    if (e.target.name === "Positive") {
      this.setState({ positiveExp: 1, neutralExp: 0, negativeExp: 0 })
    } else if (e.target.name === "Negative") {
      this.setState({ positiveExp: 0, neutralExp: 0, negativeExp: 1 })
    } else {
      this.setState({ positiveExp: 0, neutralExp: 1, negativeExp: 0 })
    }
  }

  openNote = () => {
    this.setState({ noteOpen: !this.state.noteOpen })
  }

  addEvent = async e => {
    e.preventDefault()
    const token = Cookies.get("XSRF-TOKEN")
    const event = await fetch(
      "https://stormy-basin-80765.herokuapp.com/addEvent",
      {
        body: JSON.stringify({
          event: this.state.title,
          noteId: uuid.v4(),
          lastActiveWeek: moment().format("W"),
          category: this.props.categories[this.props.category].title,
          positiveExp: this.state.positiveExp,
          neutralExp: this.state.neutralExp,
          negativeExp: this.state.negativeExp,
          note: this.state.note,
        }),
        mode: "cors", // no-cors, cors, *include
        cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
        credentials: "include", // include, *include, omit
        headers: {
          "content-type": "application/json",
          "csrf-token": token,
        },
        method: "POST",
      }
    ).catch(error => this.setState({ isError: true }))
    if (event.ok) {
      const res = await event.json()
      console.log(res.event)
      console.log("hello world")
      this.props.addEvent(res.event)
      this.setState({
        title: "",
        isError: false,
        note: "",
        noteOpen: false,
        positiveExp: 0,
        neutralExp: 1,
        negativeExp: 0,
      })
    } else {
      this.setState({
        isError: true,
      })
    }
  }

  render() {
    return (
      <div className={add.main}>
        <div className={add.row}>
          <form onSubmit={this.addEvent} className={add.form}>
            <input
              onChange={this.handleInput}
              placeholder="I recently..."
              type="text"
              name="title"
              value={this.state.title}
              className={add.input}
            />
          </form>
          <FontAwesomeIcon
            onClick={this.addEvent}
            className={add.icon}
            icon={faPlusSquare}
          />
        </div>
        <div className={add.checkHolder}>
          {this.state.error && <p className={add.error}>¯\_(ツ)_/¯ Oops...</p>}
        </div>
        <div className={add.row}>
          <div className={add.checkHolderTwo}>
            <p className={add.freq}>My experience was:</p>
            <div className={add.frequencyBox}>
              <input
                onChange={this.handleExp}
                checked={this.state.positiveExp == 1}
                name="Positive"
                type="checkbox"
              ></input>
              <p className={add.freqOpt}>Positive</p>
            </div>
            <div className={add.frequencyBox}>
              <input
                onChange={this.handleExp}
                checked={this.state.negativeExp == 1}
                name="Negative"
                type="checkbox"
              ></input>
              <p className={add.freqOpt}>Negative</p>
            </div>
            <div className={add.frequencyBox}>
              <input
                onChange={this.handleExp}
                checked={this.state.neutralExp == 1}
                name="Neutral"
                type="checkbox"
              ></input>
              <p className={add.freqOpt}>Neutral</p>
            </div>
          </div>
          <p onClick={this.openNote} className={add.blue}>
            add note +
          </p>
        </div>
        {this.state.noteOpen && (
          <textarea
            placeholder="120 characters or less.."
            onChange={this.handleInput}
            type="text"
            name="note"
            value={this.state.note}
            className={add.inputSmall}
          />
        )}
      </div>
    )
  }
}

function mapStateToProps(state) {
  return {
    category: state.activeCategory,
    categories: state.categories,
    events: state.events,
  }
}

export default connect(
  mapStateToProps,
  { addEvent }
)(Add)
